<template>
    <div>
      <div class="pt-md-5">
        <b-toast id="my-toast" variant="warning">
          <template v-slot:toast-title>
            <div class="ml-auto text-center">
              <b-img blank blank-color="#ff5555" class="" width="12" height="12"></b-img>
              <strong class="ml-1">Error!!</strong>
            </div>
          </template>
          <p class="text-center">
            <b-spinner type="grow" small class="mr-3"></b-spinner>{{errorMessage}}
            <b-spinner type="grow" small class="ml-3"></b-spinner>
          </p>
        </b-toast>
        <div class="ml-auto text-center">
            <p class="text-center mt-4 mt-md-0" style="font-weight: bolder;">
              <b-link>Login</b-link>
              /
              <b-link @click="$bvModal.hide('login-modal'); $bvModal.show('signup-modal')">Register</b-link>
            </p>
        </div>
        <div class="mx-auto mb-5 shadow-none bg-light text-center" style="width:90%;max-width: 600px;">
          <div class="mx-auto text-center" style="width:90%;max-width: 400px;">
              <b-form  class="mt-4" @submit.prevent="submitOnAction">
                  <b-form-group
                      id="input-group-2"
                      label="Phone Number:"
                      label-for="input-2"
                  >
                    <b-input-group>
                      <b-form-input
                          id="input-2"
                          type="tel"
                          required
                          v-model="phoneNo"
                      ></b-form-input>
                    </b-input-group>
                  </b-form-group>
                  <b-form-group
                      id="input-group-3"
                      label="Password:"
                      label-for="input-3"
                  >
                    <b-input-group>
                      <b-form-input
                          id="input-3"
                          :type="selectIcon?'password':'text'"
                          required
                          v-model="password"
                      ></b-form-input>
                      <b-input-group-append is-text>
                        <b-icon :icon="selectIcon?'eye-slash-fill':'eye-fill'" @click="selectIcon=!selectIcon"></b-icon>
                      </b-input-group-append>
                    </b-input-group>
                  </b-form-group>
                  <!-- <p class="text-md-center" v-if="nonexistedAccount" style="font-weight: lighter;color:red;">
                  Account doesn't exist!!</p>
                  <p class="text-md-center" v-if="nonexistedPassword" style="font-weight: lighter;color:red;">
                      Incorrect Password!!</p> -->
                  <div class="">
                    <b-overlay :show="busy" opacity="0.4" spinner-small
                      spinner-variant="primary" class="d-inline block" @hidden="onHidden">
                      <b-button  :disabled="busy" type="submit" ref="button" variant="primary">
                        Login</b-button>
                    </b-overlay>
                  </div>
              </b-form>
          </div>
        </div>
      </div>
    </div>
</template>
<script>
import store from '../../store'
import { mapActions } from 'vuex'

export default {
  name: 'LogIn',
  data () {
    return {
      busy: false,
      selectIcon: true,
      timeout: null,
      phoneNo: null,
      password: null,
      nonexistedAccount: false,
      nonexistedPassword: false,
      errorMessage: ''
    }
  },
  methods: {
    ...mapActions([
      'login'
    ]),
    onHidden () {
      this.$refs.button.focus()
    },
    submitOnAction (evt) {
      this.busy = true
      evt.preventDefault()
      this.login({
        phoneNo: this.phoneNo,
        password: this.password
      })
        .then(response => {
          // this.$router.push({ name: 'Home' })
          this.$bvModal.hide('login-modal')
        })
        .catch(error => {
          this.busy = false
          this.errorMessage = error.response.data.error
          this.showMessage()
          console.log('sorry: ' + error.response.data.error)
        })
    },
    showMessage () {
      this.$bvToast.show('my-toast')
      // this.$bvToast.toast(this.errorMessage, {
      //   title: 'Error!!',
      //   variant: 'danger',
      //   toaster: 'b-toaster-top-center',
      //   autoHideDelay: 3000
      // })
    }
  },
  beforeRouteEnter (to, from, next) {
    if (!store.getters.authToken) {
      next()
    } else {
      next({ name: 'UserProfile' })
    }
  }
}
</script>
